.trade_textContainer {
  justify-content:center;
  display:flex;
  align-items:center;
  padding:0 12px
}

.trade_title {
  font-family:var(--font-display-title);
  font-size:48px;
  text-align:center;
  margin-bottom:16px;
  line-height:48px;
  font-weight:700;
  display:inline-block;
  vertical-align:middle;
  width:auto;
  color: #fff;
}

.receive-trade-btn {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  // width: 100%;
  padding: 14px 12px;
  border-radius: 14px;
  flex-grow: 1;
  bottom: 32px;
  left: 14px;
  right: 14px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.12);
  transition: background-color ease;
  transition-duration: 0.3s;
  &:active {
    transition-duration: 0s;
    background-color: rgba(0, 122, 255, 0.3);
  }
}

.send-trade-btn {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  // width: 100%;
  padding: 14px 12px;
  border-radius: 14px;
  flex-grow: 1;
  bottom: 90px;
  left: 14px;
  right: 14px;
  cursor: pointer;
  background-color: rgba(0, 122, 255, 1);
  transition: background-color ease;
  transition-duration: 0.3s;

  &:active {
    transition-duration: 0s;
    background-color: rgba(0, 122, 255, 0.4);
  }
}

.trade-animation {
  opacity: 0; /*   */
  animation: trade-opacity 0.25s forwards; /*     */
}

@keyframes trade-opacity {
  from {
    opacity: 0; /*    */
  }
  to {
    opacity: 1; /*    */
  }
}
