.background-league {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: 100%;
  background-position: center top;
  background-repeat: no-repeat;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  transition: opacity 0.35s ease;
}

.background-shadow {
  opacity:0;
  transition:none;
  will-change:opacity
}
.background-shadowVisible {
  display:block;
  z-index:-2;
  opacity:1;
}

.theme-league {
  &__bronze {
    background-image: url("../../assets/images/bg-bronze.png");
  }
  &__silver {
    background-image: url("../../assets/images/bg-silver.png");
  }
  &__gold {
    background-image: url("../../assets/images/bg-gold.png");
  }
  &__platinum {
    background-image: url("../../assets/images/bg-platinum.png");
  }
  &__diamond {
    background-image: url("../../assets/images/bg-diamond.png");
  }
  &__squad-battle {
    background-image: url("../../assets/images/squad-battle-bg.png");
  }
}

.league-position {
  // margin-left: 42px;
  width: 120px;
  height: 120px;
  // height: 90px;
  // width: 90px;
  margin-bottom: 40px;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
}

.league-icon {
  &__bronze {
    background-image: url("../../assets/images/level_bronze.png");
  }
  &__silver {
    background-image: url("../../assets/images/level_silver.png");
  }
  &__gold {
    background-image: url("../../assets/images/level_gold.png");
  }
  &__platinum {
    background-image: url("../../assets/images/level_platinum.png");
  }
  &__diamond {
    background-image: url("../../assets/images/level_diamond.png");
  }
}

.league-animation {
  opacity: 0; /*   */
  animation: league-opacity 0.5s forwards; /*     */
}

@keyframes league-opacity {
  from {
    opacity: 0; /*    */
  }
  to {
    opacity: 1; /*    */
  }
}

.league_skeleton {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 20px 0;
  list-style: none;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.12);
  &__wrapper {
    width: 100%;
    height: 50px;
    position: relative;
  }
}

// circle
.league_skeleton__circle {
  position: absolute;
  // border-radius: 100%;
  left: 60px;
  width: 48px;
  height: 48px;
  background-color: rgba(255, 255, 255, 0.04);
  display: flex;
  justify-content: center;
  align-items: center;
  &_miners {
    border-radius: 50%;
  }
  &_squads {
    border-radius: 32%;
  }
}

