.boost-loader {
  width: 16px;
  min-width: 16px;
  height: 16px;
  min-height: 16px;
  border: 2.6px solid #fff;
  border-bottom-color: transparent;
  border-radius: 50%;
  animation: boost-rotation 1.5s linear infinite;
  margin-left: 4px;
  margin-right: 14px;
  margin-top: 4px;
  margin-bottom: 4px;
}

@keyframes boost-rotation {
  0% {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.boost-left-btn {
  position: relative;
  width: 100%;
  border-radius: 16px;
  height: 50px;
  padding: 46px 12px;
  background-color: rgba(255, 255, 255, 0.12);
  align-items: center;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-flex;
  transition: background-color ease;
  transition-duration: 0.3s;
  &:active {
    transition-duration: 0s;
    background-color: #29292c;
  }
}

