    align-items: center;
    gap: 10px;
    padding: 16px 12px;
    width: 100%;
    margin-top: 23px;
  }
  &__center {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 112px 12px;
    margin-top: 12px;
  }
  &__center-content {
    display: flex;
    align-items: center;
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 45px 12px;
    margin-top: 14px;
  }
  &__list-content {
    display: flex;
    align-items: center;
  }
  &__header-btn-content {
    display: flex;
    align-items: center;
  }
}

