.open-details-btn {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  // width: 100%;
  padding: 14px 12px;
  border-radius: 14px;
  background-color: transparent;
  flex-grow: 1;
  bottom: 8px;
  left: 14px;
  right: 14px;
  cursor: pointer;
  transition: background-color ease;
  transition-duration: 0.3s;
  &:active {
    transition-duration: 0s;
    background-color: #29292c;
  }
}

.fren-animation {
  opacity: 0; /*   */
  animation: fren-opacity 0.5s forwards; /*     */
}

@keyframes fren-opacity {
  from {
    opacity: 0; /*    */
  }
  to {
    opacity: 1; /*    */
  }
}


.fren_skeleton {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 20px 0;
  list-style: none;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.12);
  &__wrapper {
    width: 100%;
    height: 50px;
    position: relative;
  }
}

// circle
.fren_skeleton__circle {
  position: absolute;
  border-radius: 100%;
  left: 20px;
  width: 48px;
  height: 48px;
  background-color: rgba(255, 255, 255, 0.04);
  display: flex;
  justify-content: center;
  align-items: center;
}

.fren_skeleton__pulse {
  position: absolute;
  left: 20px;
  height: 12px;
  width: 12px;
  background-color: #000;
  border-radius: 50%;
  margin-top: 34px;
  margin-left: 30px;
  border: 2px solid #000;
}

